import React, { useState, useEffect, useContext, useId, useRef } from 'react';
import { Context } from '../../context/IdContext';
import './Grid.css';
import { useDrag } from 'react-dnd';
import { Context as FormContext } from '../../context/FormContext';
export const Rented = ({
	w,
	className,
	rented,
	index,
	containerWidth,
	containerLeft,
	containerWidth2,
	containerLeft2,
	dragRef,
	containerRef,
	onmouseDownHandler,
	inView,
	...props
}) => {
	const { state: { id }, idMsg } = useContext(Context);

	const [ width, setWidth ] = useState(0);
	const [ left, setLeft ] = useState(0);
	const rentedRef = useRef(null);

	const { state, formSubmittedMsg } = useContext(FormContext);
	// const { formSubmitted } = state;

	useEffect(() => {
		if (rentedRef.current !== null) {
			const RR = rentedRef.current.getBoundingClientRect();
			const left = RR.left - containerLeft;
			setLeft(left);
			setWidth(containerWidth - left);
		}
	});

	const [ { hover, isDragging }, drag ] = useDrag(() => ({
		type: 'RENTED',
		item: {
			rented
		},

		// begin: (item, monitor) => console.log('BEGIN'),
		end: (item, monitor) => formSubmittedMsg(true),
		collect: (monitor) => {
			return {
				isDragging: !!monitor.isDragging()
			};
		}
	}));

	return (
		<div
			ref={drag}
			style={{
				display: 'flex',
				boxSizing: 'border-box',
				height: '30px'
			}}
		>
			<div
				style={{
					borderTop: '15px solid transparent',
					borderRight: '10px solid #5abe9d',
					borderBottom: '15px solid transparent'
				}}
			/>

			<div
				ref={rentedRef}
				// key={id}
				// onMouseOver={() => idMsg(rented)}
				// onMouseOut={() => idMsg(false)}
				className={className}
				style={{
					display: 'flex',
					justifyContent: 'flex-start',
					cursor: 'pointer',
					width: `${rented.width / 100 * (w * 2) - 21}px`
				}}
			>
				<div
					className="name-container-container"
					style={{
						width: `${width - 40}px`,
						textAlign: 'center',
						overflowX: 'hidden'
					}}
				>
					<div
						className="name-container"
						style={{
							paddingLeft: `${left <= 0 ? -left : 0}px`,

							boxSizing: 'border-box',
							color: 'black'
						}}
					>
						{rented.id}
					</div>
				</div>
			</div>
			<div
				style={{
					borderTop: '15px solid transparent',
					borderLeft: '10px solid #5abe9d',
					borderBottom: '15px solid transparent'
				}}
			/>
		</div>
	);
};
