import createDataContext from './createDataContext';

const MSGS = {
	ID: 'ID',
	SOCKET: 'SOCKET'
};
const idMsg = (dispatch) => (id) => {
	dispatch({
		type: MSGS.ID,
		id
	});
};
const socketMsg = (dispatch) => (ws) => {
	dispatch({
		type: MSGS.SOCKET,
		ws
	});
};

const IdContext = (state, msg) => {
	switch (msg.type) {
		case MSGS.ID: {
			const { id } = msg;

			return { ...state, id };
		}
		case MSGS.SOCKET: {
			const { ws } = msg;

			return { ...state, ws };
		}

		default:
			return state;
	}
};
export const { Context, Provider } = createDataContext(
	IdContext,
	{
		idMsg,
		socketMsg
	},
	{
		id: '',
		ws: ''
	}
);
