import React, { useState } from 'react';
import './App.css';
import { ContainerContainer } from './components/Container/ContainerContainer';
import { Provider } from './context/ThumbContext';
import { Provider as DatesProvider } from './context/DatesContext';
import { Provider as FormProvider } from './context/FormContext';
import { Provider as IdProvider } from './context/IdContext';
import { HTML5Backend } from 'react-dnd-html5-backend';
import { DndProvider } from 'react-dnd';

export default function App() {
	console.log('Appppppp');
	const [ data, setData ] = useState({
		email: '',
		end: '2023/01/06',
		id: '63b58f79e18806c7473f348f',
		name: '',
		nights: '1',
		phone: '',
		price: '2000',
		room: '0',
		start: '2023/01/05'
	});

	return (
		<IdProvider>
			<FormProvider>
				<DatesProvider>
					<Provider>
						<DndProvider backend={HTML5Backend}>
							<ContainerContainer />
						</DndProvider>
					</Provider>
				</DatesProvider>
			</FormProvider>
		</IdProvider>
	);
}
